/* ====== COVID Only Alert Bar ======== */

.covid-alert-bar {
    .alert-flex {
        border-bottom: 4px solid #c4b581;
        background: #f1ead6;
    }
    .covid-general-strip {
        background: #f0e1b0;
        width: 100%;
        /*text-align: center;*/
        /*border-top: 4px solid #7fd2c6;*/
        border-bottom: 4px solid #c4b581;
        h2,
        div {
            max-width: 1140px;
            margin: 0 auto;
        }
        h2 {
            padding: 2rem 5% 0;
            font-size: 1.2rem;
            color: #5d4b3f;
            @media screen and (min-width: 720px) {
                font-size: 26px;
            }
        }
        div {
            padding: 1rem 5%;
        }
        p {
            display: flex;
            flex-direction: column;
            max-width: 1140px;
            margin: 0 auto;
            /*justify-content: space-evenly;*/
            /*align-items: center;*/
            /*white-space:nowrap;*/
            @media screen and (min-width: 720px) {
                flex-direction: row;
            }
        }
        strong,
        span,
        a {
            display: block;
            padding: 1rem 2%;
            flex: 1 1 100%;
            width: 100%;
            text-align: center;
            font-size: 1rem;
            line-height: 1.2;
        }
        strong {
            color: #c4b581;
            font-weight: 600;
        }
        span {
            color: #000;
            font-weight: 300;
        }
        a {
            background: #fff;
            border: 1px solid #5d4b3f;
            border-radius: 4px;
            color: #5d4b3f;
            text-align: center;
            margin: 0 0 1rem;
            font-weight: 400;
            @media screen and (min-width: 720px) {
                margin: 0 2% 1rem 0;
                &:last-child {
                    margin-right: 0;
                }
            }

            &:hover,
            &:focus {
                background: rgba(196, 181, 129, .5);
                color: #777;
            }
        }
    }

    /* ======= Covid Text Content Blocks ================ */
    .covid-alert-block {
        width: 100%;
        padding: 2rem 10%;
        background: #f0e1b0;

        h2,
        h3,
        strong,
        &:not(side-nav) a:hover,
        p a:hover {
            color: #5d4b3f;
            max-width: unset;
        }
        h2 {
            margin: 0 0 1rem 0;
            width: 100%;
            @media screen and (min-width: 720px) {
                margin: 2rem 0;
            }
        }
        h3 {
            margin-top: 2rem;
            @media screen and (min-width: 960px) {
                margin-top: 0;
            }
        }
        p,
        li {
            font-size: 1rem;
            line-height: 1.5;
            margin-bottom: .5rem;
        }
        li li {
            margin-bottom: 0;
        }
        ul {
            /*list-style: none;*/
            padding-left: 1.5rem;
        }
        a:not(.button-default) {
            display: inline;
            background: none;
            padding: 0;
            text-decoration: underline;
        }
        a.button-default {
            display: block;
            padding: 1rem;
            background: #ffe9a8;
            border-radius: 9px;
            /*color: #004e38;*/
            text-align: center;
            &:visited,
            &:hover {
                background: #fdf3d6;
                border-color: #fff;
            }
        }

        aside {
            width: 100%;
            padding: 1rem 10%;
            background: #ffc107;
            text-align: center;
            font-size: 1.3rem;
            color: #00573d;
        }
        @media screen and (min-width: 720px) {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            margin: 0 auto;
            padding: 2rem 5% 1rem;
            width: 100%;
            max-width: 1140px;
            div[class*="sub-"] {
                display: flex;
                margin: 1rem auto;
            }
            .sub-2cols,
            .sub-3cols {
                flex: 1 0 46%;
                margin-bottom: 0;
                margin-right: 8%;
                flex-direction: column;
                justify-content: flex-start;
                &:last-child {
                    margin-right: 0;
                }
            }
        }
        @media screen and (min-width: 960px) {
            .sub-3cols {
                flex: 0 1 30%;
                margin-right: 5%;

                &:last-child {
                    margin-right: 0;
                }
            }
        }
        @media screen and (min-width: 1200px) {
            padding: 2rem 0;
        }
    }
}
